












import { Vue, Component, Watch } from 'vue-property-decorator';

@Component
export default class Header extends Vue {
  private useMainPageStyles = true;

  created() {
    this.checkRoute();
  }

  @Watch('$route')
  checkRoute() {
    if (this.$route.path !== '/') {
      this.useMainPageStyles = false;
    } else {
      this.useMainPageStyles = true;
    }
  }
}
